body {
    /* background: linear-gradient(0deg, rgba(97,97,171,1) 0%, rgba(57,48,74,1) 100%); */
    background: linear-gradient(0deg, rgba(255, 255, 255,1) 0%, rgba(255,164,172,1) 100%);
    overflow: hidden;
    font-family: Arimo;
    font-weight: bold;
    -webkit-user-select: none;    
    -moz-user-select: none;
    user-select: none;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}

.Name {
    position: flex;
    vertical-align: middle;
    text-align: center;
    font-family: monospace;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    font-size: 10vw;
}

.About {
    position: flex;
    text-align: center;
    font-size: 4vw;
}

.About a:hover {
    color: #fff;
}

.Skills {
    position: flex;
    text-align: center;
    font-size: 2vw;
}
